/* FAQ Block Question Heading */
.sp-faq-question {
	display: flex;
	align-items: center;
	gap: 0.5em; /* spacing between marker and text */
	font-weight: 600;
}

/* Marker common styles */
.faq-marker {
	flex-shrink: 0; /* prevent marker from shrinking */
	font-weight: 600;
}

/* Decimal numbers (1., 2., 3.) */
.faq-marker.marker-decimal {
	/* default numeric marker, no extra styling needed */
}

/* Disc bullet */
.faq-marker.marker-disc {
	/* optional styling for disc */
}

/* Circle bullet */
.faq-marker.marker-circle {
	/* optional styling for circle */
}

/* Square bullet */
.faq-marker.marker-square {
	/* optional styling for square */
}

/* FAQ Answer */
.sp-faq-answer {
	margin-top: 0.25em;
	margin-bottom: 0.75em;
	line-height: 1.5;
}

/* FAQ Image */
.sp-faq-image {
	margin-top: 0.5em;
}

.sp-faq-image img {
	max-width: 100%;
	height: auto;
	display: block;
}
